@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;600;800&family=Montserrat:wght@600;800&family=Raleway+Dots&display=swap');


*{
  margin:0;
}

.container {
  background-color: black;
  color: white;
  padding: 25px 45px;
  display: flex;
  position: sticky; 
  top:0;
  justify-content: space-between;
  font-family: 'Montserrat';
  
  border-bottom: 1px solid white;
  
  
}


.header {
 
  display: flex;
  align-items: center;

  
}
.header > a {
  text-decoration: none;
}

.name-logo {
  font-size: 40px;
  border: 2px solid white;
  background-color: orange;
  color: white;
  border-radius: 30px;
  padding: 5px 15px;
  font-weight: 800;
}

.nav > li {
  display: inline;
  list-style: none;
  padding: 0 15px;
 
  
}

.nav-link {
  text-decoration: none;
  color: white;
  cursor: pointer;
  font-size: 1.2em;
  
}

.nav-link:hover {
  color:orange;
}



@media (max-width:828px) {
  .name-logo {
    font-size: 20px;
  
  }
  
  .nav > li {
   
    padding: 0 7px;
  }
  
  .nav-link {
   
    font-size: 0.9em;
    
  }
  .container {
   
    padding: 25px 25px;
  }
}


