@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;600;800&family=Montserrat:wght@600;800&family=Raleway+Dots&display=swap');


.contact {
    background-color: black;
    color: white;
    border-bottom: 1px solid white;
    font-family: 'Montserrat';
    display: flex;
    padding: 80px 0;
    font-family: 'Montserrat';
    justify-content: center;
    align-self: center;
    align-items: center;
    width: 100%;
   
}


.contact-me {
    flex-direction: column;
    display: flex;
    width: 500px;
    align-items: center;
   
}

.contact-me > h3 {
    font-size: 35px;
    margin-bottom: 35px;
}

.contact-me > button {
   margin-top: 25px;
   width: fit-content;
}

.messages {
   
    display: flex;
    padding: 25px 0 0 25px;
    align-items: center;
   

}


.contact-me > input {
    padding: 15px 10px;
    margin: 5px 10px;
    font-size: 25px;
    background-color: black;
    color: white;
    border: 0;
    border-bottom: 2px solid orange;
    text-transform: capitalize;
}

.contact-me > input::placeholder {
    font-size: 15px;
    color: white;
    font-family: 'Montserrat';
}


.messages > textarea {
    border: 2px solid orange;
    background-color: black;
    color: white;
    font-size: 25px;
    border-radius: 10px;
    width: 500px;
    height: 300px;
    padding: 5px;
    
    
}

.messages > textarea::placeholder {
    font-size: 15px;
    color: white;
    padding: 5px 5px;
    font-family: 'Montserrat';
}


@media (max-width:828px) {

    .contact-me {
        width: 150px;
        align-items: center;
        padding: 25px;
       
    }
    .contact-me > h3 {
        font-size: 20px;
        margin-bottom: 35px;
    }
    
    .contact-me > button {
       margin-top: 25px;
       width: fit-content;
    }

    .contact-me > input {
        padding: 15px 5;
        margin: 5px 10px;
        font-size: 15px;
        width: 150px;
        background-color: black;
        color: white;
        border: 0;
        border-bottom: 2px solid orange;
        text-transform: capitalize;
    }
    
    .contact-me > input::placeholder {
        font-size: 12px;
        color: white;
        font-family: 'Montserrat';
    }

    .messages > textarea {
        width: 150px;
        height: 200px;
        font-size: 15px;
        padding-top: 5px;
        
        
    }
    
}


