@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;600;800&family=Montserrat:wght@600;800&family=Raleway+Dots&display=swap');



.logo {
    width:25px;
}

.footer {
    padding: 65px 70px;
    background-color: black;
    color: white;
    font-family: 'Montserrat';
    
    
}

footer {
    border-bottom: 1px solid white;
    padding-bottom: 5px;
}

.top {
    
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.center > span {
    font-size: 15px;
}

.social  {
    display: flex;
    align-items: center;
  
    
}

.social > h4 {
    font-weight: 100;
    padding: 5px 10px;
}

.social >  li {
    list-style: none;
    display: flex;
    padding: 0 7px;
    align-self: center;
    
}


@media (max-width:828px) {
    .center > span {
        font-size: 10px;
        
    }
    .social > h4 {
        display: none;
        
     }
     .center {
        width: 130px;
     }
}

