@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;600;800&family=Montserrat:wght@600;800&family=Raleway+Dots&display=swap');


.projects {
    background-color: black;
    color: white;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-self: center;
    height: fit-content;
    padding-bottom: 80px;
    border-bottom: 1px solid white;
    font-family: 'Montserrat'; 
}

.projects-site {
  width: fit-content;
  align-self: center;
  text-align: center;
  margin-top: 25px;
  
  

}

.title-section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 80px;
    padding-bottom: 80px;
}

.title-section > h3 {
  font-size: 35px;
}

.title-section > p {
  padding-top: 20px;
}


.line {
    border-bottom-style: solid;
    border-bottom-width: 5.1px;
    border-bottom-color: orange;
    padding-bottom: 5px;
    width: fit-content;
    margin: 0  auto auto;  
}


.container-project {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 80px;
  padding-right: 80px;
  
}


.project-one {
  text-align: center;
  width: 350px;
  height: 472px;
  border: 1px solid white;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  opacity: 0.5; 
  
 
}

.photo-projects {
    width: 250px;
    height: 303px;
    border: 3px solid darkorange;
    border-radius: 10px;
    padding: 0;
}

.project-one:hover  {
    opacity: 1;
    transition: .7s ease;
  }

.project-one > h3 ,.project-one > p {
    padding: 15px;
}


  
@media (max-width:828px){
  .title-section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 40px;
    padding-bottom: 40px;
}

.title-section > h3 {
  font-size: 20px;
}

.title-section > p {
  padding-top: 10px;
}
.project-one > h3 ,.project-one > p {
  padding: 8px;
}

.container-project {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
}
.project-one {
  text-align: center;
  width: 250px;
  height: fit-content;
  margin: 8px 0;
 
  
}
.project-one > h3  {
  padding: 10px;
  font-size: 18px;
}

.project-one > p {
  padding: 10px;
  font-size: 15px;
}
.photo-projects {
  width: 230px;
  height: 253px;
}
.projects-site > button {
  font-size: 25px;
}
}

  
