@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;600;800&family=Montserrat:wght@600;800&family=Raleway+Dots&display=swap');



.about {
     width: 100%;
     font-family: 'Montserrat';
     border-bottom: 1px solid gray;
     height: fit-content;
     background-color: black;
     text-align: center;
     color: white;
     padding-bottom: 80px;
     display: flex;
     flex-direction: column;
     align-items: center;
 }
 

 
 .description {
     
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: center;
     align-self: center;
     
     
     
 }

 .description > h3 {
   padding-top: 50px;
    
    font-size: 35px;
 }

 .description > p {
    padding-bottom: 25px;
    padding-top: 15px;
    width: 80%;
 }

 
 .skills {
      display: flex;
      margin-top: 55px;
     justify-content: space-between;
 }
 
.numberskills, .project-desc {
   width: 50%;
}

 .numberskills > ul {
    list-style: none;
    padding: 5px;
    
 }
 
 .numberskills > h3, .project-desc > h3 {
    margin-bottom: 35px;
    font-size: 25px;
    
    
 }

 .line {
             
    /* Increase this as per requirement */
   
    border-bottom-style: solid;
    border-bottom-width: 5.1px;
    border-bottom-color: orange;
    padding-bottom: 5px;
    width: fit-content;
    
    
}


   



 .numberskills > ul > li {
     border: 1px solid white;
     width: fit-content;
     padding: 5px 10px;
     display: inline-block;
     text-align: center;
     font-size: 25px;
     background-color: black;
     color: white;
     margin-top: 5px;
     margin-right: 5px;
     margin-bottom: 5px;
     margin-left: 0;
 }


 .project-desc > p {
    padding: 5px 35px;
    margin-bottom: 15px;
 }
 

.project-desc > button{
 margin-top: 15px;

}
 button {
   font-family: 'Montserrat' ;
   border: 1px solid orange;
   padding: 5px 10px;
   color: white;
   background-color: black;
   font-size: 20px;
   cursor: pointer;
   font-weight: bold;
   
 }

 button:hover {
   background-color: orange;
   color: black;
   border: 1px solid darkorange;
   padding: 10px 20px;
 }


 @media (max-width: 828px) {
   .description > h3 {
    font-size: 20px;
    
   }
   .description > p, .project-desc > p  {
      font-size: 10px;
      
     }

     .numberskills ul > li {
      font-size: 15px;
      padding: 2px 5px;
      margin:2px 4px;
     }
     .numberskills > h3, .project-desc > h3 {
      margin-bottom: 15px;
      font-size: 15px;
      
      
   }
   button {
      font-family: 'Montserrat' ;
      border: 1px solid orange;
      padding: 2px 5px;
      color: white;
      background-color: black;
      font-size: 10px;
      cursor: pointer;
      font-weight: bold;
      
    }
   
    button:hover {
      background-color: orange;
      color: black;
      border: 1px solid darkorange;
      padding: 5px 10px;
    }
 }
 